import { lazy, Suspense } from 'react';
import { useParams } from 'react-router-dom';
import Loading from '../App/Loading';
import Pagination from '../Components/Pagination';
import useFetch from '../Hooks/useFetch';

export default function ProjectEuler() {

  const
  { page } = useParams<{ page: string }>(),
  LazyImport = lazy(
    () => import('./ProjectEuler/P' + (page || 1))
  ),

  { data: JS, ready: readyJS } = useFetch('/Projects/Project-Euler/P' + (page || 1) + '.js'),
  { data: PY, ready: readyPY } = useFetch('/Projects/Project-Euler/P' + (page || 1) + '.py');

  return readyJS && readyPY && (
    <Pagination lastPage={30}>
      <Suspense fallback={Loading()}>
        <LazyImport lang={(() => {
          let lang: any = {};
          if (!JS.includes('<noscript>')) lang['JavaScript'] = JS;
          if (!PY.includes('<noscript>')) lang['Python'] = PY;
          return lang;
        })()} />
      </Suspense>
    </Pagination>
  );
};